
.whiteStyle {
  min-height: 700px;
  padding: 20px 300px;
  display: block;
  text-align: center;
  background-color: white;
  font-size: 15pt;
  color: black;
}

.blackStyle {
  min-height: 700px;
  padding: 20px 300px;
  display: block;
  text-align: center;
  background-color: black;
  font-size: 15pt;
  color: white;
}

html {
  scroll-behavior: smooth;
}

.arrowDownDiv {
  display: flex;
  justify-content: center;
}

.whiteStyle a.arrowDown {
  display: inline-block;
  align-self: flex-end;
  color: black;
}

.blackStyle a.arrowDown {
  display: inline-block;
  align-self: flex-end;
  color: white;
}